<template>
  <div class="login-container">
    <div class="login-header">
      <div class="login-logo">
        <img src="../../images/login_logo.png" alt="">
        XPE 智能办公平台
      </div>
    </div>
    <!-- 输入新密码 -->
    <div class="login-box">
      <div class="title">
        输入新密码
      </div>
      <div class="login-form margint_50">
        <div class="login-Item">
          <input type="password" placeholder="输入新密码" class="login-input" name="" v-model="newPwd" @blur="checkNewpwd">
          <em v-if="newPwdError" class="error">{{newPwdMsg}}</em>
        </div>
        <div class="login-Item">
          <input type="password" placeholder="再次输入新密码" class="login-input" name="" v-model="newPwd1" @blur="checkNewpwd2">
          <em v-if="newPwdError1" class="error">{{newPwdMsg1}}</em>
        </div>
        <div><input class="login-btn" :class="{'login-btn-disabled':submitOk}" type="button" value="确  认" @click="submitClick">
        </div>
        <router-link to="/login"><a class="login-link">已有账号，去登录>></a></router-link>
      </div>
    </div>
    <vue-particles
        color="#e1e1e1"
        :particleOpacity="0.5"
        :particlesNumber="15"
        shapeType="circle"
        :particleSize="15"
        linesColor="#cfcfcf"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="650"
        :moveSpeed="2"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="false"
        clickMode="push"
      >
      </vue-particles>
  </div>
</template>

<script>
import {apiChangepwdbyPhone} from '../../config/api'
import {mapState, mapMutations} from 'vuex'
import {dialogConfirm} from '../../config/mUtils'
export default {
  name: 'HelloWorld',
  data () {
    return {
      phoneNum:'',//手机号
      newPwd:'',//新密码
      newPwdMsg:'',
      newPwdMsg1:'',
      newPwd1:'',//再次输入新密码
    }
  },
  created(){
    if(!this.$route.query.phoneNum){
      this.$router.push({
        path:'/findpwd'
      }); 
    }else{
      this.phoneNum=this.$route.query.phoneNum;
    }
    
  },
  components:{

  },
  computed: {
    newPwdError:function(){
      if(this.newPwd!=''&&this.newPwd.length<6){
        return true;
      }else{
        return false
      } 
    },
    newPwdError1:function(){
      if(this.newPwd1!=''&&!this.newPwdError){
        if(this.newPwd1==this.newPwd){
          return false
        }else{
          return true
        }
      }else{
        return false
      }
    },
    submitOk:function(){
      if(this.newPwd1==''||this.newPwd==''||this.newPwdError1||this.newPwdError){
        return false
      }else{
        return true
      }
    }
  },
  methods:{
    checkNewpwd(){  
      if(this.newPwdError){
        this.newPwdMsg='密码不少于6位'
      }else{
        this.newPwdMsg=''
      }
    },
    checkNewpwd2(){ 
      if(this.newPwdError1){
        return '两次输入的密码不一致';
      }else{
        return ''
      }
    },
    submitClick(){
      if(this.submitOk){
        //console.log(this.phoneNum);
        apiChangepwdbyPhone({
          phone:this.phoneNum,
          password:this.newPwd
        }).then(res=>{
          if(res.data.msgNo==1){
            // this.$message({
            //   message:"密码修改成功",
            //   type:'success'
            //  })
            dialogConfirm('success','密码修改成功');
            this.$router.push({
              path:'/login',
            });
          }
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../style/login';
</style>


